import React, { useState, useEffect } from "react";  
import MockContent from './mockContent'; 
import MinimapSection from '../miniMapView/miniMapView'; 
import './detailedViewStyle.scss';
import { useLocation } from 'react-router-dom';

const DetailedView = () => {
    let contentToHighlight = [];
    contentToHighlight.push('here<div style="background-color: green;">Green text goes here</div>here');
  
    const location = useLocation();
    const [contents, setContents] = useState([]);
    const [loading, setLoading] = useState(false);
    const [error, setError] = useState();
    const [searchQuery, setSearchQuery] = useState("");
    const [submitForm, setSubmitForm] = useState();
    const [showMinimap, setShowMinimap] = useState(false);

    async function doFetch() {
      const searchParams = new URLSearchParams(location.search);
      const queryParam = searchParams.get("query");
      const lastSegment = queryParam && queryParam.substring(queryParam.lastIndexOf("/") + 1);
      //`https://en.m.wikipedia.org/wiki/${lastSegment}`,
        const response = await fetch(`https://en.m.wikipedia.org/w/rest.php/v1/page/${lastSegment}/html`);
        const data = await response.text();
        return data;
      }
      
      async function fetchAsync()
      {
        try {
          let contents = await doFetch();
          setContents(contents);
        } catch( err ) {
        }
      }

    useEffect(() => {
        fetchAsync();
    }, []);

    const handleChange = (event) => {
      setSearchQuery(event.target.value);
    }

  useEffect(() => {
    const timeoutId = setTimeout(() => {
      setSubmitForm(searchQuery);
      searchQuery && searchQuery.length && setShowMinimap(true);
    }, 1000);
    return () => clearTimeout(timeoutId);
  }, [searchQuery]);

    return(
        <div className="page-section" >
          <input placeholder="Enter search query" className="search-query" type="text" name="name" value={searchQuery} onChange={handleChange}/>
          <hr/>
          <MockContent
              contents={contents}
          />
          <MinimapSection
              contentToHighlight={contentToHighlight}
              submitForm={submitForm}
              showMinimap={showMinimap}
          />
        </div>
    )
}

export default DetailedView;
