import React, { useEffect, useState } from "react";
import './miniMapViewStyle.scss';
import Minimap from 'minimap-react/lib/react-minimap';
import "minimap-react/lib/react-minimap.css";
import { debounce } from "lodash";

const MinimapSection = ({ contentToHighlight, submitForm, showMinimap }) => {
  const [isScrolling, setScrolling] = useState(showMinimap);

  const scrollListener = () => {
    setScrolling(true);
  }

  const scrollStop = () => {
      setScrolling(false);
  }

  useEffect(() => {
    window.addEventListener('scroll', function() {
        scrollListener();
    }, false);

    window.addEventListener('scroll', debounce(() => { scrollStop() }, 5000))
    return () => window.removeEventListener("scroll", scrollListener); //
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  return (
    <div>
      { isScrolling  &&
        <div className="minimap-container" id="tests">
          <Minimap 
              rootId="root"
              position="right" 
              smoothScroll={true} 
              smoothScrollDelay={200} 
              toggle={true}
              // contentToHighlight={contentToHighlight}
              // searchQuery={submitForm}
              // heightRatio={1}
              // widthRatio={2}
          />
        </div>
      }
    </div>
  );
};

export default MinimapSection;
